<template>
  <v-app>
    <mb-right-sidebar></mb-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link" exact> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-expansion-panels v-model="panel" multiple>
              <v-expansion-panel v-for="(network,i) in networks" :key="i">
                <v-expansion-panel-header hide-actions>
                  <template v-slot:default="{open}">
                    <b>NETWORK: {{network.name}}</b>
                    <v-icon style="text-align: end; display: block;">
                      {{open ? 'mdi-chevron-down' : 'mdi-chevron-right' }}
                    </v-icon>
                  </template>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-data-table
                    v-if="dataTable[network.name].length > 0"
                    v-model="selected"
                    :headers="advancedView ? advancedHeaders : pointHeaders"
                    :items="dataTable[network.name]"
                    class="elevation-1 text-left"
                    item-key="uuid"
                    show-select
                    :item-class="backgroundColor"
                    :footer-props="tableProps.FOOTER_PROPS"
                    :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                    :search="search"
                    sort-by="uuid"
                    group-by="category"
                    show-group-by
                    :show-expand="advancedView"
                    single-expand
                    @item-expanded="expandItem"
                    :expanded="expanded"
                    dense
                  >
                    <template v-slot:top>
                      <v-toolbar flat>
                        <v-toolbar-title>Points View</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <div class="text-left pa-2">
                          <v-btn @click="onClickRefresh" x-small color="primary" class="white--text"> Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;
                          <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" >Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                          <v-btn @click="advancedView = !advancedView; expanded =[]" x-small color="purple" class="white--text" > Advanced View <v-icon x-small right dark> {{advancedView ? 'mdi-playlist-minus': 'mdi-playlist-plus'}} </v-icon> </v-btn>&nbsp;
                        </div>
                        <v-spacer></v-spacer>
                        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                      </v-toolbar>
                    </template>

                    <template v-slot:group.header="{ group, headers, toggle, isOpen, items }">
                      <td :colspan="headers.length">
                        <div class="row" style="align-items: center;">
                          <v-btn @click="toggle" x-small icon :ref="group">
                            <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon>
                          </v-btn>&nbsp;&nbsp;&nbsp;&nbsp;
                          <v-checkbox v-bind:value="selected.filter(point=>point.device_uuid === items[0].device_uuid).length == items.length" @change="check($event, items)"></v-checkbox>
                          <router-link :to="`/modbus/devices/${items[0].device_uuid}/points`" class="font-weight-bold text-style"> {{group}} </router-link>
                        </div>
                      </td>
                    </template>

                    <template v-slot:item.created_on="{ item }" >
                      {{item.created_on | timezone}}
                    </template>
                    <template v-slot:item.updated_on="{ item }" >
                      {{item.updated_on | timezone}}
                    </template>
                    <template v-slot:item.ts_value="{ item }" >
                      {{item.ts_value | timezone}}
                    </template>
                    <template v-slot:item.ts_fault="{ item }" >
                      {{item.ts_fault | timezone}}
                    </template>
                    <template v-slot:item.enable="{ item }">
                      <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:item.writable="{ item }">
                      <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:item.history_enable="{ item }">
                      <v-checkbox v-model="item.history_enable" @click="updatePoint(item)"></v-checkbox>
                    </template>
                    <template v-slot:item.register="{ item }">
                      <v-edit-dialog :return-value.sync="item.register" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                        <b>{{ item.register }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.register" single-line type='number' min="0" @input="numberChange()" ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.register_lesngth="{ item }">
                      <v-edit-dialog :return-value.sync="item.register_length" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.register_length }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.register_length" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.function_code="{ item }">
                      <v-edit-dialog :return-value.sync="item.function_code" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.function_code }}</b>
                        <template v-slot:input>
                          <v-select v-model="item.function_code" :items="point_types" ></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.data_type="{ item }">
                      <v-edit-dialog :return-value.sync="item.data_type" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.data_type }}</b>
                        <template v-slot:input>
                          <v-select v-model="item.data_type" :items="point_data_types"></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.data_endian="{ item }">
                      <v-edit-dialog :return-value.sync="item.data_endian" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.data_endian }}</b>
                        <template v-slot:input>
                          <v-select v-model="item.data_endian" :items="point_data_endians"></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_round="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.value_round }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_offset="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_offset" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.value_offset }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.value_offset" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value_raw="{ item }">
                      <v-edit-dialog :return-value.sync="item.value_raw" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.value_raw }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.value_raw" single-line></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                  <template v-slot:item.value_operation="{ item }">
                    <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                      <b>{{ item.value_operation }}</b>
                      <template v-slot:input>
                        <v-text-field v-model="item.value_operation" single-line type='string'></v-text-field>
                      </template>
                    </v-edit-dialog>
                  </template>
                    <template v-slot:item.priority_array_write="{ item }" >
                      <div class="overflow-td">
                        <span v-for="(key, index) of item.priority_array_write ? Object.keys(item.priority_array_write): []" :key="index" >
                          {{key}}:&nbsp;<span class="value">{{ item.priority_array_write[key] || item.priority_array_write[key] == 0  ? item.priority_array_write[key] : 'null' }}</span>,&nbsp;
                        </span>
                      </div>
                    </template>
                    <template v-slot:item.fault_message="{ item }" >
                      <div class="overflow-td">
                        {{item.fault_message}}
                      </div>
                    </template>
                    <template v-slot:item.value_raw="{ item }" >
                      <div class="overflow-td">
                        {{item.value_raw}}
                      </div>
                    </template>
                    <template v-slot:item.history_interval="{ item }">
                      <v-edit-dialog :return-value.sync="item.history_interval" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.history_interval }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.history_interval" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.cov_threshold="{ item }">
                      <v-edit-dialog :return-value.sync="item.cov_threshold" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.cov_threshold }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.cov_threshold" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.history_type="{ item }">
                      <v-edit-dialog :return-value.sync="item.history_type" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.history_type }}</b>
                        <template v-slot:input>
                          <v-select v-model="item.history_type" :items="history_data_types" ></v-select>
                        </template>
                      </v-edit-dialog>
                    </template>
                    <template v-slot:item.value="{ item }">
                      <v-edit-dialog :return-value.sync="item.value" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                        <b>{{ item.value }}</b>
                        <template v-slot:input>
                          <v-text-field v-model="item.value" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>

                    <template v-slot:item.actions="{ item }">
                      <v-icon small class="mr-2" @click="editItem(item), expanded= []" color="primary"> mdi-pencil </v-icon>
                      <v-icon small @click="clickDelete(item)" color="error"> mdi-delete </v-icon>
                    </template>

                    <!-- header -->
                    <template v-slot:header.enable="{ item }">
                      {{ toUpperTextKey('enable') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.writable="{ item }">
                      {{ toUpperTextKey('writable') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.write_value_once="{ item }">
                      {{ toUpperTextKey('write_value_once') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('write_value_once')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.register_length="{ item }">
                      {{ toUpperTextKey('register_length') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('register_length')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.data_type="{ item }">
                      {{ toUpperTextKey('data_type') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('data_type')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.data_endian="{ item }">
                      {{ toUpperTextKey('data_endian') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('data_endian')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.value_round="{ item }">
                      {{ toUpperTextKey('value_round') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.value_operation="{ item }" >
                      {{ toUpperTextKey('value_operation') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.cov_threshold="{ item }">
                      {{ toUpperTextKey('cov_threshold') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('cov_threshold'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.input_min="{ item }">
                      {{ toUpperTextKey('input_min') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('input_min')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.input_max="{ item }">
                      {{ toUpperTextKey('input_max') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('input_max')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.scale_min="{ item }">
                      {{ toUpperTextKey('scale_min') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('scale_min')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.scale_max="{ item }">
                      {{ toUpperTextKey('scale_max') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('scale_max')" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>
                    <template v-slot:header.value="{ item }">
                      {{ toUpperTextKey('value') }}&nbsp;
                      <v-icon x-small @click="openMassEditDialog('value'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
                    </template>

                    <!-- expand-edit-row -->
                    <template v-slot:item.data-table-expand="{ expand, isExpanded }">
                      <v-icon @click="expand(!isExpanded)">{{ isExpanded ? 'mdi-menu-up' : 'mdi-menu-down' }}</v-icon>
                    </template>
                    <template v-slot:expanded-item="{ headers, item }">
                      <td colspan="2"></td>
                      <td class="actions">
                        <v-icon small @click="updatePoint(editedItem)" color="primary"> mdi-content-save </v-icon>
                      </td>
                      <td class="name">
                        <v-text-field v-model="editedItem.name" required :rules="[() => !isExistingvalue(editedItem.name, 'name', editedItem) || 'Name Existed', rules.required]" ></v-text-field>
                      </td>
                      <td class="driver"></td>
                      <td class="enable">
                        <v-checkbox v-model="editedItem.enable" ></v-checkbox>
                      </td>
                      <td class="function_code">
                        <v-select v-model="editedItem.function_code" :items="point_types" required :rules="[rules.required]"></v-select>
                      </td>
                      <td class="register">
                        <v-text-field v-model="editedItem.register" :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']" type="number" ></v-text-field>
                      </td>
                      <td class="register_length">
                        <v-text-field v-model="editedItem.register_length" type="number" ></v-text-field>
                      </td>
                      <td class="cov_threshold">
                        <v-text-field v-model="editedItem.cov_threshold" type="number"></v-text-field>
                      </td>
                      <td class="data_type">
                        <v-select v-model="editedItem.data_type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                      </td>
                      <td class="data_endian">
                        <v-select v-model="editedItem.data_endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                      </td>
                      <td class="value">
                        <v-text-field v-model="editedItem.value" type="number"></v-text-field>
                      </td>
                      <td class="value_original"></td>
                      <td class="value_round">
                        <v-text-field v-model="editedItem.value_round" type="number"></v-text-field>
                      </td>
                      <td class="value_offset">
                        <v-text-field v-model="editedItem.value_offset" type="number"></v-text-field>
                      </td>
                      <td class="value_operation">
                        <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                      </td>
                      <td class="value_raw"></td>
                      <td class="input_min">
                        <v-text-field v-model="editedItem.input_min" type="number"></v-text-field>
                      </td>
                      <td class="input_max">
                        <v-text-field v-model="editedItem.input_max" type="number"></v-text-field>
                      </td>
                      <td class="scale_min">
                        <v-text-field v-model="editedItem.scale_min" type="number"></v-text-field>
                      </td>
                      <td class="scale_max">
                        <v-text-field v-model="editedItem.scale_max" type="number"></v-text-field>
                      </td>
                      <td class="writable">
                        <v-checkbox v-model="editedItem.writable"></v-checkbox>
                      </td>
                      <td colspan="9"></td>
                    </template>
                  </v-data-table>
                  <div v-else>No data</div>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
            <v-overlay :value="loadingData">
              <v-progress-circular indeterminate size="64" color="primary"></v-progress-circular>
            </v-overlay>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server='modbus'></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Point</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field @input="changeName" v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem.name, 'name', editedItem) || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.writable" label="writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.write_value_once" label="Repeat Point Write"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-select v-model="editedItem.function_code" label="function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-select v-model="editedItem.data_type" label="data type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-select v-model="editedItem.data_endian" label="data endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.register" label="register" :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.register_length" label="register length" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.value_offset" label="value offset" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" > </v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="History" disabled class="centered-input"></v-text-field>
                </v-col>
                <!-- <v-col cols="12" sm="6"  md="3">
                  <v-checkbox v-model="editedItem.history_enable" label="Enable"></v-checkbox>
                </v-col> -->
                <v-col cols="12" sm="6"  md="3">
                  <v-select v-model="editedItem.history_type" label="Type" :items="history_data_types" ></v-select>
                </v-col>
                <v-col cols="12" sm="6"  md="3">
                  <v-text-field v-model="editedItem.history_interval" label="Interval" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="updatePoint(editedItem)" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-select v-if="editField === 'data_type'" v-model="editAll[editField]" :items="point_data_types" :label="toUpperTextKey(editField)" ></v-select>
            <v-select v-if="editField === 'data_endian'" v-model="editAll[editField]" :items="point_data_endians" :label="toUpperTextKey(editField)" ></v-select>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'function_code' && editField !== 'data_type' && editField !== 'data_endian'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, TOPIC, POINT_HEADERS, POINT_ADVANCED_HEADERS, POINT_TYPES, POINT_DATA_TYPES, POINT_DATA_ENDIANS, HISTORY_DATA_TYPES, POINT_EDIT_ALL_DEFAULT, } from './modbusConstant';
import { toUpperTextKey, conventionNaming } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog,
    SystemStatusTable,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    point_types: [...POINT_TYPES],
    point_data_types: [...POINT_DATA_TYPES],
    point_data_endians: [...POINT_DATA_ENDIANS],
    history_data_types: [...HISTORY_DATA_TYPES],
    rules: {...RULES},
    editAll: {...POINT_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogDelete: false,
    dialogMassEdit: false,
    tab: 0,
    action: '',
    network: {},
    valid: false,
    selected: [],
    editedItem: { },
    search: '',
    editField: "",
    intervalId: null,
    expanded: [],
    pointsOfDevice: [],
    dataTable: {},
    dirs: [{text: "NETWORKS", link: '/modbus/networks'}, { text: 'POINTS VIEW', disabled: true }],
    advancedView: false,
    networks: [],
    panel: [0],
    tableProps: {...DATA_TABLE_PROPS},
    loadingData: false,
  }),
  computed: {
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getAllPoints();
      }
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getAllPoints();

    //set auto refresh
    if(this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      this.loadingData = true;
      axios.get(`point-server/network?with_children=true&points=true`).then(data => {
        this.networks = data;
        this.getDataTable(data);
        this.loadingData = false;
      });
    },
    getDataTable(networks){
      this.dataTable = {};
      networks.forEach(network=>{
        this.dataTable[network.name] = [];
        network.devices.forEach(device=>{
          if(device.points.length > 0){
            const pointsPerDevice = device.points.map(point=>{
              const keys = Object.keys(point.point_store);
              if (keys && keys.length > 0) {
                keys.forEach(key => {
                  point = { ...point, [key]: point.point_store[key], category: `DEVICE: ${device.name}`, network_name: network.name };
                });
              }
              delete point.point_store;
              delete point.point_uuid;
              return point;
            });
            this.dataTable[network.name] = this.dataTable[network.name].concat(pointsPerDevice);
          }
        });
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable[item.network_name].findIndex(p=>p.uuid == item.uuid);
      this.pointsOfDevice = this.dataTable[item.network_name].filter(point=>point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable[this.editedItem.network_name].indexOf(this.editedItem);
      this.dataTable[this.editedItem.network_name].splice(this.editedIndex, 1);
      this.dataTable[this.editedItem.network_name] = [...this.dataTable[this.editedItem.network_name]];
      await axios.delete(`point-server/point/${this.editedItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p=>p.uuid == item.uuid);
      points.splice(index, 1);
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      axios.put(`point-server/point/${data.uuid}`, data)
        .then(response => {
          const index = this.dataTable[data.network_name].findIndex(n => n.uuid == response.uuid);
          Object.assign(this.dataTable[data.network_name][index], response);
          this.$toasted.show('update success');
          this.close();
          this.expanded = [];
        });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll= {...POINT_EDIT_ALL_DEFAULT};
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({item}) {
      this.editedIndex = this.dataTable[item.network_name].indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh(){
      this.getAllPoints();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if(!e) this.selected = [...this.selected].filter(point=>point.device_uuid != items[0].device_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    changeName(val){
      this.editedItem.name = conventionNaming(val, this.setting);
    },
  },
};
</script>
<style scoped>
@import '../../assets/css/tableView.css';
</style>
